<template>
  <table className='table table-striped table-hover'>
        <thead>
            <tr>
                <th>#</th>
                <th>Fecha</th>
                <th>Monto</th>
                <th>Eliminar</th>
            </tr>     
        </thead>
        <tbody>
 
            <tr  v-for="fac, index in getFacturasAll" :key="fac._id">
                <td>{{index + 1}}</td>
                <td>{{fac.fecha.slice(0,10)}}</td>
                <td>{{fac.monto}}</td>

                  <td>
                    <button
                        className='btn btn-secondary'
                        @click="setFactura(fac)"
                        :disabled="getIsLoading"
                    >
                        Editar
                    </button>
                </td> 
                <td>
                    <button
                        className='btn btn-danger'
                        @click="deleteFactura(fac)"
                        :disabled="getIsLoading"
                    >
                        Eliminar
                    </button>
                </td> 

            </tr>

        </tbody>
    
    </table>
    
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex';

export default {
   
    computed:{
        ...mapGetters('facturar',['getFacturasAll','getIsLoading','getId'])

    },
    methods:{
        ...mapActions('facturar',['getFacturas','deleteFactura']),
        ...mapMutations('facturar',['setFactura'])
    },
    created(){
        this.getFacturas(this.getId)
    }
}
</script>

